.sidebar {
  min-width: 300px;

  @include vp--1279 {
    min-width: 200px;
  }

  @include vp--1023 {
    min-width: 100%;
    padding: 0 24px;
  }

  @include vp--767 {
    padding: 0 16px;
  }

  &__list {
    list-style: none;
    margin: 0;
    padding: 0;

    @include vp--1023 {
      display: flex;
      margin-left: -20px;
    }

    @include vp--374 {
      margin-left: -10px;
    }
  }

  &__item {
    margin-bottom: 20px;

    @include vp--1023 {
      margin-left: 20px;
    }

    @include vp--374 {
      margin-left: 10px;
    }
  }

  &__link {
    text-transform: uppercase;

    opacity: 1;
    transition: opacity $trans--300;

    @include vp--1023 {
      text-transform: none;
    }

    @include vp--500 {
      font-size: 12px;
      line-height: 18px;
    }

    &:hover,
    &:focus {
      opacity: 0.6;
    }
  }

  &__link--active {
    text-decoration: underline;
    pointer-events: none;
  }
}
