.input-radio {
  position: relative;

  width: 140px;
  height: 100px;

  display: flex;
  align-items: center;
  justify-content: center;

  @include vp--1439 {
    width: 80px;
    height: 40px;
  }

  @include vp--1023 {
    width: 60px;
    height: 40px;
  }

  @include vp--500 {
    width: 50px;
    height: 40px;
  }

  label {
    text-align: center;
    color: $color-default-black;
    z-index: 1;

    @include vp--1439 {
      font-size: 10px;
      line-height: 10px;
    }

    @include vp--500 {
      font-size: 8px;
      line-height: 8px;
    }
  }

  input[type="radio"] {
    appearance: none;
  }

  input[type="radio"]::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    background-color: $color-firefly;
    cursor: pointer;
  }

  input[type="radio"]:checked::before {
    background-color: $color-gulf-stream;
  }
}
