.form {
  &__col {
    margin-bottom: 60px;

    @include vp--1023 {
      margin-bottom: 20px;
    }
  }

  &__row {
    display: flex;
    flex-direction: row;

    &--buttons {
      margin-top: 100px;
      justify-content: space-between;

      @include vp--1023 {
        margin-top: 50px;
        max-width: 500px;
      }

      .button {
        margin-right: 10px;

        &:last-child {
          margin-right: 0;
        }

        @include vp--1023 {
          padding: 10px;
        }

        @include vp--500 {
          padding: 5px 10px;

          font-size: 12px;
          line-height: 20px;
        }
      }
    }

    .input-radio {
      margin-right: 20px;

      @include vp--1023 {
        margin-right: 10px;
      }
    }
  }
}
