.main-nav {
  display: flex;
  width: 100%;

  &__wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;

    @include vp--1023 {
      justify-content: flex-end;
    }
  }

  &__list-menu {
    display: flex;
    justify-content: center;
    align-items: center;

    .main-nav__close-button {
      display: none;
    }

    @include vp--1023 {
      display: none;
    }
  }

  &__list {
    display: flex;
    justify-content: center;
    align-items: center;

    margin: 0;
    padding: 0;

    list-style: none;

    .main-nav__item {
      margin-left: 40px;

      @include vp--1023 {
        margin-left: 0;
      }

      &--none {
        display: none;
      }
    }

    &--buttons {
      .main-nav__item {
        margin-left: 20px;

        @include vp--1023 {
          margin-left: 10px;
        }

        &--reg {
          @include vp--500 {
            display: none;
          }
        }
      }
    }
  }

  &__link {
    text-transform: uppercase;

    opacity: 1;
    transition: opacity $trans--600;

    &:hover,
    &:focus,
    &:active {
      opacity: 0.4;
    }
  }

  &__toggle.button {
    display: none;

    @include vp--1023 {
      display: block;
    }
  }

  &--opened {
    .main-nav__list-menu {
      @include vp--1023 {
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        z-index: 1000;

        display: flex;
        flex-shrink: 0;
        justify-content: center;
        align-items: center;
        width: 100%;
        min-height: 100%;
        padding: 60px 40px;

        background-color: $color-default-white;

        .main-nav__close-button {
          display: block;
        }

        .main-nav__list {
          flex-direction: column;

          .main-nav__link {
            color: $color-default-black;
          }
        }
      }
    }
  }
}
