.intro {
  &__wrapper {
    display: flex;
    justify-content: space-between;

    @include vp--1023 {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }

  &__text {
    max-width: 600px;
    margin-right: 40px;

    @include vp--1023 {
      margin-bottom: 40px;
      margin-right: 0;
    }
  }

  &__link {
    text-decoration: underline;

    opacity: 1;
    transition: opacity $trans--600;

    &:hover,
    &:focus,
    &:active {
      opacity: 0.6;
    }
  }

  &__img {
    img {
      height: 100%;
      min-width: 400px;
      object-fit: contain;

      @include vp--500 {
        min-width: 100%;
        width: 100%;
      }
    }
  }
}
