.button {
  display: flex;
  justify-content: center;
  align-items: center;

  padding: 15px 40px;
  background: transparent;
  outline: none;
  border: none;

  cursor: pointer;

  opacity: 1;
  transition: opacity $trans--600;

  @include vp--1023 {
    padding: 10px 20px;
  }

  &:hover,
  &:focus,
  &:active {
    opacity: 0.6;
  }

  &--circle {
    border-radius: 50px;
    background-color: $color-default-white;
    color: $color-white-smoke;
  }

  &--border-circle {
    border-radius: 50px;
    border: 1px solid $color-grey;
    background-color: $color-transparent;
    color: $color-grey;
  }
}
