.header {
  display: flex;
  align-items: center;
  min-height: 100px;
  padding: 20px 40px;

  @include vp--1023 {
    padding: 10px 24px;
  }

  @include vp--767 {
    padding: 10px 16px;
  }

  &__wrapper {
    display: flex;
    align-items: center;
    width: 100%;
  }

  &__logo {
    width: 100px;
    height: 80px;

    @include vp--1023 {
      width: 60px;
      height: 60px;
    }

    svg {
      object-fit: contain;
      width: 100px;
      height: 80px;

      @include vp--1023 {
        width: 60px;
        height: 60px;
      }
    }
  }
}
