.card-about {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 400px;
  width: 30%;
  padding: 40px 20px;

  background-color: $color-green;

  @include vp--1023 {
    min-height: 200px;
    width: 100%;
    margin-bottom: 20px;
  }

  p {
    text-align: center;
    font-weight: bold;
    font-size: 28px;
    line-height: 45px;

    @include vp--500 {
      font-size: 18px;
      line-height: 35px;
    }

    @include vp--374 {
      font-size: 16px;
      line-height: 32px;
    }
  }

  svg {
    opacity: 1;
    transition: opacity $trans--600;
  }

  &:hover svg,
  &:focus svg,
  &:active svg {
    opacity: 0.4;
  }
}
